import { React, useState } from "react";
import styled from 'styled-components';
import background from "../images/timeline/process_background.png";
import Button from "../component/Button";
import none from "../images/none.png";
import { useMediaQuery } from 'react-responsive';

const TimelineContainer = styled.div`
    background-color: transparent;
`

const InfoContainer = styled.div`
    height: 90vh;
    width: 100%;
    margin-top: 0;
    margin-bottom: 40px;
    background-image: url(${background});
    background-size: contain;
    background-repeat: no-repeat;
    background-position: bottom;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
`

const TitleText = styled.div`
    font-size: clamp(1.5rem, 1vw, 1.8rem);
    font-weight: 600;
    background-color: transparent;
`

const GrayTitleText = styled.div`
    font-weight: 600;
    font-size: clamp(0.6rem, 1vw, 0.8rem);
    color: #949494;
    background-color: transparent;
`

const GraySubtitleText = styled.div`
    font-weight: 400;
    width: 100%;
    font-size: clamp(0.6rem, 100vw, 0.8rem);
    color: #949494;
    background-color: transparent;
    margin-top: 20px;
    text-align: center;
    line-height: 140%;
`

const SubtitleText = styled.div`
    font-size: clamp(0.6rem, 2vw, 0.8rem); 
    font-weight: 200;
    color: #B2B2B2;
    background-color: transparent;
`

const PartContainer = styled.div`
    width: 80%;
    background-color: transparent;
    margin-top: 40px;
    display: flex;
    flex-direction: column;
    align-items: center
`

const Part = styled.div`
    background-color: transparent;
    display: flex;
    flex-direction: column;
    align-items: center;
`

const TextButtonContainer = styled.div`
    background-color: transparent;
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 100%;
`

const TextButton = styled.div`
    width: 80%;
    height: 30px;
    font-size: clamp(1.1rem, 1.5vw, 1.3rem);
    font-weight: 600;
    background-color: transparent;
    cursor: pointer;
    padding-right: 0.5rem;
`

const ContentText = styled.div`
    display: flex;
    flex-wrap: wrap;
    justify-content: center; 
    font-weight: 400;
    background-color: transparent;
    padding-bottom: 10px;
    width: 90%;
    max-width: 80%;
    text-align: center; 
    margin: 0 auto; 
    box-sizing: border-box; 
    overflow: hidden; 
    align-items: center;
`

const WhiteText = styled.span`
    font-size: clamp(0.8rem, 2vw, 1rem); 
    font-weight: 400;
    background-color: transparent;
`

const BlueText = styled.span`
    color: #0057FF;
    font-size: clamp(0.8rem, 2vw, 1rem);
    font-weight: 400;
    background-color: transparent;
`

const GraySmallSubtitleText = styled.span`
    font-size: clamp(0.6rem, 2vw, 0.7rem);
    font-weight: 200;
    color: #B2B2B2;
    background-color: transparent;
    margin-left: 5px;
`

const TimetableContainer = styled.div`
    background-color: transparent;
    display: flex;
    justify-content: center;
    align-items: center;
`;

const TimetableImage = styled.div`
    width: 40vw;
    height: 50vh;
    background-image: url(${none});
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    border-radius: 8px;
`;


const TimelinePage = () => {
    const isMobile = useMediaQuery({ query: "(max-width: 768px)" });
    const [isDS, setIsDS] = useState(true);

  const handleClick = (part) => {
    if (part === 'DS' && !isDS) {
      setIsDS(true);
    } else if (part === 'Dev' && isDS) {
      setIsDS(false);
    }
  };

    return (
        <TimelineContainer>
            <InfoContainer>
                <TitleText></TitleText>
                <GrayTitleText>APPLY</GrayTitleText>
                <SubtitleText>24. 09. 08 (SUN) 10:00 - 24. 09. 14 (SAT) 23:59</SubtitleText>

                { isMobile &&
                    <>
                        <PartContainer>
                            <TextButtonContainer>
                                <TextButton
                                    onClick={() => handleClick('DS')}
                                    style={{
                                    color: isDS ? "#0057FF" : "#767676",
                                    borderBottom: isDS ? "2px solid #FFFFFF" : "2px solid #767676"
                                    }}
                                    disabled={isDS}
                                >
                                     
                                </TextButton>
                                <TextButton
                                    onClick={() => handleClick('Dev')}
                                    style={{
                                    color: !isDS ? "#0057FF" : "#767676",
                                    borderBottom: !isDS ? "2px solid #FFFFFF" : "2px solid #767676"
                                    }}
                                    disabled={!isDS}
                                >
                                    
                                </TextButton>
                            </TextButtonContainer>

                            { isDS && <>
                                <WhiteText style={{ marginTop: "10px", alignSelf: "flex-start"}}>DACOS 2   OB</WhiteText>
                                <GraySubtitleText style={{ marginBottom: "50px"}}>
                                    * 5,000  ,    .<br />
                                    *   ,       .<br />
                                    *          .
                                </GraySubtitleText>
                                <Button
                                    onClick={() => {
                                        window.open("https://forms.gle/6KaV4nXxNoFtnS6p7") //   
                                    }}
                                    style = {{ height: "40px", width: "100px", alignSelf: "center"}}
                                    >
                                    
                                </Button>
                            </>
                            }

                            { !isDS && <>
                                <WhiteText style={{ marginTop: "10px", alignSelf: "flex-start"}}>SOLUX 29   OB     </WhiteText>
                                <GraySubtitleText style={{ marginBottom: "50px"}}>
                                    * 5,000  ,    .<br />
                                    *   ,       .<br />
                                    *          .
                                </GraySubtitleText>

                                <Button
                                    onClick={() => {
                                        window.open("https://forms.gle/1Bt1vyWGVtyYf2SH9") //   
                                    }}
                                    style = {{ height: "40px", width: "100px"}}
                                    >
                                    
                                </Button>
                            </>
                            }
                        </PartContainer>
                    </>
                }
                
                { !isMobile && 
                    <>
                    <PartContainer>
                        <Part style={{ marginTop: "10px"}}>
                            <BlueText> </BlueText>   
                            <WhiteText>DACOS 2   OB</WhiteText>
                        </Part>
                        <Part style={{ marginTop: "10px"}}>
                            <BlueText></BlueText>
                            <WhiteText>SOLUX 29   OB     </WhiteText>
                        </Part>
                    </PartContainer>
                    <GraySubtitleText style={{ marginBottom: "50px"}}>
                        * 5,000  ,    .<br />
                        *   ,       .<br />
                        *          .
                    </GraySubtitleText>

                    <Button
                        onClick={() => {
                            window.open("https://linktr.ee/DxSHackathon")
                        }}
                        style = {{ height: "40px", width: "100px"}}
                        >
                        
                    </Button>
                    </>
                }
                
            </InfoContainer>
            
            <InfoContainer>
                <TitleText> </TitleText>
                <GrayTitleText>TEAM REVEAL</GrayTitleText>
                
                <SubtitleText>2024. 10. 31 (THU)</SubtitleText>
                
                <ContentText style={{ marginTop: "60px"}}>
                    <WhiteText>    </WhiteText> <BlueText>9 20 </BlueText> <WhiteText>.</WhiteText>
                </ContentText>
                <ContentText>
                    <WhiteText>   2,  2,  2   .</WhiteText> <GraySmallSubtitleText>(   )</GraySmallSubtitleText>
                </ContentText>
                <ContentText>
                    <WhiteText>    </WhiteText> <BlueText>  </BlueText> <WhiteText>.</WhiteText>
                </ContentText>
                <GraySmallSubtitleText>*   </GraySmallSubtitleText>
            </InfoContainer>

            <InfoContainer>
                <TitleText></TitleText>
                <GrayTitleText>PRE - PREPARATION</GrayTitleText>
                
                <SubtitleText>2024. 11. 01 (FRI) - 24. 11. 07 (THU)</SubtitleText>
                
                <ContentText style={{ marginTop: "60px"}}>
                    <WhiteText>         </WhiteText> <BlueText>    </BlueText> <WhiteText>.</WhiteText>
                </ContentText>
                <ContentText>
                    <BlueText> - </BlueText> <WhiteText>     .</WhiteText> <GraySmallSubtitleText>(  )</GraySmallSubtitleText>
                </ContentText>
                <ContentText>
                    <WhiteText>      ,     .</WhiteText>
                </ContentText>

            </InfoContainer>

            <InfoContainer style={{ marginBottom: "20px"}}>
                <TitleText>  </TitleText>
                <GrayTitleText>HACKATHON</GrayTitleText>
                
                <div style={{ display: "flex", alignItems: "center"}}><SubtitleText>2024. 11. 08 (FRI) 10:00 - 24. 11. 09 (SAT) 14:00</SubtitleText> <GraySmallSubtitleText>*  2 </GraySmallSubtitleText></div>
                
                <ContentText style={{ marginTop: "10px"}}>Time table</ContentText>
                <TimetableContainer>
                    <TimetableImage />
                </TimetableContainer>

                <GraySubtitleText style={{ marginTop: "5px"}}>*, ,      </GraySubtitleText>
            </InfoContainer>
        </TimelineContainer>
    );
};

export default TimelinePage;
